import { Input as AntDInput } from 'antd';
import React, { forwardRef } from 'react';

import { getInputEmotionStyles } from './Input';
import type { PasswordProps } from './common';
import { DesignSystemAntDConfigProvider } from '../DesignSystemProvider';
import { useDesignSystemTheme } from '../Hooks/useDesignSystemTheme';
import { addDebugOutlineIfEnabled } from '../utils/debug';
import { safex } from '../utils/safex';

export const Password: React.FC<React.PropsWithChildren<PasswordProps>> = forwardRef<AntDInput, PasswordProps>(
  function Password(
    { validationState, autoComplete = 'off', dangerouslySetAntdProps, dangerouslyAppendEmotionCSS, ...props },
    ref,
  ) {
    const { classNamePrefix, theme } = useDesignSystemTheme();
    const useNewShadows = safex('databricks.fe.designsystem.useNewShadows', false);
    const useNewBorderRadii = safex('databricks.fe.designsystem.useNewBorderRadii', false);

    return (
      <DesignSystemAntDConfigProvider>
        <AntDInput.Password
          {...addDebugOutlineIfEnabled()}
          visibilityToggle={false}
          ref={ref}
          autoComplete={autoComplete}
          css={[
            getInputEmotionStyles(classNamePrefix, theme, { validationState, useNewShadows, useNewBorderRadii }),
            dangerouslyAppendEmotionCSS,
          ]}
          {...props}
          {...dangerouslySetAntdProps}
        />
      </DesignSystemAntDConfigProvider>
    );
  },
);
